.shopTitle {
    margin-top: 100px;
    text-align: center;
    font-size: 40px;
  }
  .products {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  }
   .product {
    border-radius: 15px;
    width: 300px;
    height: 350px;
    margin: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
   .product img {
    width: 400px;
  }
   .product .description {
    text-align: center;
  }
   .product:hover {
    transition: 0.3s ease-in;
    cursor: pointer;
  }
   .addToCartBttn {
    background-color: gray;
    border: 2px solid rgb(19, 19, 19);
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
 
  .addToCartBttn:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
  }

  /* shop.css */
.shopTitle {
  margin: auto;
  padding: 0 1rem; /* Add padding on smaller screens */
}

.products {
  display: flex;
  flex-wrap: wrap; /* Ensure items can wrap on small screens */
  justify-content: center; /* Center items for a nice layout */
  gap: 1rem; /* Add some space between items */
}

.product {
  flex: 1 1 200px; /* Example: Allow flex items to grow and shrink, but not smaller than 200px */
  /* Adjust other styles as necessary */
}

/* General CSS for images in cart items and shop products */
img {
  max-width: 100%;
  height: auto;
  display: block; /* Remove extra space below the image */
}
