*, *:before, *:after {
  box-sizing: border-box;
  max-width: 100%;
}



.about-us-container {
  /* position: relative; /* Enable positioning for the overlay */
  /* width: 100%; 
  height: 100%;
  background-color: #f2f2f2; */ 


  margin: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-overlay {
  padding: 35px;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure image covers the entire container */
  opacity: 0; /* Initially hide the image */
  transition: opacity 0.5s ease-in-out; /* Add smooth transition */
}

.background-image.loaded {
  opacity: 1; /* Show the image once loaded */
}

.loading-animation {
  /* Styles for your loading animation (e.g., spinner, placeholder text) */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* Adjust styles as needed for your chosen animation */
}

.featured-products-1{
  
  display: flex;
  justify-content: center; /* Centers children horizontally */
  align-items: center; /* Centers children vertically */
  gap: 30px; /* Adjust the gap between items as necessary */
  margin: 50px; /* Margin around the whole container */
  padding:30px;

}

.image-holder-1{

  flex: 1; /* Allows the image holder to grow and take up space */
  display: flex;
  order:1;
  flex-direction: column;
  min-height:20vh;
  min-width: 20vw;;

  
}

.image-holder-2{

  flex: 1; /* Allows the image holder to grow and take up space */
  display: flex;
  flex-direction: column;
  order: 2;
  min-height:20vh;
  min-width: 20vw;;

  
}

.content-holder-1{

  flex: 1; /* Allows the content holder to grow and take up space */
